@import url(https://fonts.googleapis.com/css?family=Questrial);
@import url(https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.7.0/animate.min.css);
@import url(https://use.fontawesome.com/releases/v5.6.3/css/all.css);
body {
  font-family: 'Questrial', sans-serif;
  background: linear-gradient(38deg, #bcffee, #ffe0a3);
  background-size: 200vw 200vh;
  -webkit-animation: bg-anim 19s ease infinite;
          animation: bg-anim 19s ease infinite;
  color: #3a5750;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@-webkit-keyframes bg-anim { 
  0%{background-position:0% 18%}
  50%{background-position:100% 60%}
  100%{background-position:0% 18%}
}

@keyframes bg-anim { 
  0%{background-position:0% 18%}
  50%{background-position:100% 60%}
  100%{background-position:0% 18%}
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.name {
  margin-top: 20vh;
  font-size: 120px;
  line-height: 1.1em;
}

.tagline {
  font-size: 70px;
}

.underline {
  text-decoration: underline;
}

::-moz-selection {
  background-color: rgba(143, 103, 16, 0.459);
}

::selection {
  background-color: rgba(143, 103, 16, 0.459);
}

.link {
  position: relative;
  color: #3a5750;
  text-decoration: none;
  transition: 0.3s;
}

.link:hover {
  color: gray;
  text-decoration: none;
  cursor: pointer;
}

.link::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: gray;
  visibility: hidden;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  transition: all 0.3s ease-in-out 0s;
}

.link:hover::before {
  visibility: visible;
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

i[class^='fa'] {
  color: #3a5750;
  transition: 300ms;
}

i[class^='fa']:hover {
  color: gray;
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}

.bgcanvas {
  position: fixed;
  height: 100%;
  width: 100%;
}

.go-btn {
  transition: 300ms;
}

.bob-anim {
  -webkit-animation: bob 2s infinite;
          animation: bob 2s infinite;
}

.go-btn:hover {
  cursor: pointer;
  color: #22c4e4;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

@-webkit-keyframes bob {
  0% { -webkit-transform: translate(0,0); transform: translate(0,0) }
  50% { -webkit-transform: translate(0,-5px); transform: translate(0,-5px) }
  100% { -webkit-transform: translate(0,0); transform: translate(0,0) }
}

@keyframes bob {
  0% { -webkit-transform: translate(0,0); transform: translate(0,0) }
  50% { -webkit-transform: translate(0,-5px); transform: translate(0,-5px) }
  100% { -webkit-transform: translate(0,0); transform: translate(0,0) }
}

#main-col {
  transition: 500ms;
}
